import { useDispatch, useSelector } from "react-redux";
import { addCart, removeCart } from "@Storages/Cart/Cart";

export function useCart() {
  const dispatch = useDispatch();
  const currentCart = useSelector((state) => state.Cart.value);

  function add({ product, quality = 1 }) {
    dispatch(
      addCart({
        id: product.id,
        quality: quality,
      })
    );
  }

  function remove({ product, quality = 1 }) {
    dispatch(
      removeCart({
        id: product.id,
        quality: quality,
      })
    );
  }

  return {
    add: add,
    remove: remove,
    current: currentCart,
  };
}
