const generateMockData = function () {
  var data = [];
  for (var i = 1; i <= 21; i++) {
    data.push({
      id: `product${i}`,
      name: {
        "zh-TW": `產品名稱 ${i}`,
        "en-US": `Silent Forest Hair Clip Set ${i}`,
      },
      image: require(`@Mocks/product${i}.jpg`),
      price: 300,
    });
  }
  return data;
};

const App = {
  all: generateMockData(),
};

export default App;
